import React from 'react'
import { graphql } from 'gatsby'
import Helmet from 'react-helmet'

import Layout from '../components/layout'
import * as toc from '../../left-toc.json';
import { push } from "../../node_modules/gatsby-link/index";
import { isApi } from '../templates/site-config';
import { urlCheck } from '../templates/layout';

var routes = toc.path;
var route;
for (var path of routes) {
  if (!urlCheck(path)) {
    route = path;
    break;
  }
}
var value = toc.demos;

var demosMapper = {
  'https://ej2.syncfusion.com/demos': 'JavaScript',
  'https://ej2.syncfusion.com/javascript/demos': 'JavaScript (ES5)',
  'https://ej2.syncfusion.com/angular/demos': 'Angular',
  'https://ej2.syncfusion.com/react/demos': 'React',
  'https://ej2.syncfusion.com/vue/demos': 'Vue',
  'https://ej2.syncfusion.com/aspnetcore/': 'ASP.NET Core',
  'https://ej2.syncfusion.com/aspnetmvc/': 'ASP.NET MVC',
  'https://ej2.syncfusion.com/blazor/': 'Blazor [Preview]'
}

class BlogIndex extends React.Component {
  componentDidMount() {
    if (isApi) {
      var pathSplit = routes[0].split('/');
      pathSplit.pop();
      push(pathSplit.join('/'));
    } else {
      push(route);
    }
  }

  render() {
    return (
      <Layout location={this.props.location}>
        <Helmet
          htmlAttributes={{ lang: 'en' }}
          meta={[{ name: 'description', content: 'Syncfusion ' }]}
          title={'Essential JS 2 for ' + demosMapper[value] + ' | Syncfusion Documentation' }
        />
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
query {
  allMarkdownRemark {
    edges { 
      node {
        fields {
          slug
        }
      }
    }
  }
}
`
